<template>
  <div style="text-align:center">
    <ul>
      <li
        v-for="qnaItem in QnaLists"
        v-bind:key="qnaItem.no"
        @click="getQna(qnaItem.no)"
      >
        {{ qnaItem.no }} | {{ qnaItem.title }} | {{ qnaItem.id }} |
        {{ qnaItem.regdate }}
        <!-- 자기 아이디로 작성한 글만 -->
        <button type="button" @click.stop="updateQna(qnaItem.no)">수정</button>
        <button type="button" @click.stop="removeQna(qnaItem.no)">삭제</button>
      </li>
    </ul>

    검색하기
    <select id="searchCondition" size="1" name="key" v-model="key">
      <option value="all">검색조건</option>
      <option value="id">아이디</option>
      <option value="title">제목</option>
      <option value="contents">내용</option>
    </select>
    검색 단어 <input type="text" v-model="word" />
    <button @click="search">검색</button>
  </div>
</template>

<script>
import Constant from "../Constant";

export default {
  name: "QnaList",
  data() {
    return {
      key: "",
      word: ""
    };
  },
  computed: {
    QnaLists() {
      return this.$store.state.QnaLists;
    }
  },
  created() {
    this.$store.dispatch(Constant.GET_QnaLIST);
  },
  methods: {
    updateQna(no) {
      this.$router.push("/qna/update/" + no);
    },
    getQna(no) {
      this.$router.push("/qna/detail/" + no);
    },
    removeQna(no) {
      this.$store.dispatch(Constant.REMOVE_Qna, { no });
    },
    search() {
      this.$store.dispatch(Constant.SEARCH_Qna, {
        key: this.key,
        word: this.word
      });
    }
  }
};
</script>

<style scoped></style>
