<template>
  <div style="text-align:center">
    <h2>상세정보</h2>
    아이디 : {{ Qna.id }}
    <br />
    번호 : {{ Qna.no }}
    <br />
    내용 : {{ Qna.contents }}
    <br />
    등록시간 : {{ Qna.regdate }} <br />새로운 댓글을 입력 :
    <input v-model="newComment.comment" />
    <button @click="addComment(Qna.id, Qna.no)">등록</button>
    <div v-for="comment in CommentLists" v-bind:key="comment.cno">
      {{ comment.id }} : {{ comment.comment }} : {{ comment.cregdate }}
      <button @click="deleteComment(comment.cno)">삭제</button>
      <br />
    </div>
  </div>
</template>

<script>
import Constant from "../Constant";
export default {
  name: "detailpage",
  data() {
    return {
      Qna: {},
      CommentLists: [],
      newComment: {
        qno: 0,
        comment: "",
        id: ""
      }
    };
  },
  mounted() {
    this.getComments();
    this.getQnalist();
  },
  created() {
    this.newComment.qno = this.$route.params.no;
  },
  methods: {
    getComments() {
      console.log("댓글들을 불러옵니다.");
      this.$store
        .dispatch(Constant.GET_Comments, { qno: this.newComment.qno })
        .then(() => {
          this.CommentLists = { ...this.$store.state.CommentLists };
          console.log(this.CommentLists);
        });
    },
    getQnalist() {
      this.$store
        .dispatch(Constant.GET_Qna, { no: this.$route.params.no })
        .then(() => {
          this.Qna = { ...this.$store.state.Qna };
        });
      console.log("Aa" + this.$route.params.no);
    },
    addComment(id, qno) {
      if (this.newComment.comment.trim() != "") {
        this.newComment.qno = qno;
        this.newComment.id = id;
        console.log("id<<<<<" + qno);
        this.$store
          .dispatch(Constant.ADD_COMMENT, {
            qno: this.newComment.qno,
            id: this.newComment.id,
            comment: this.newComment.comment
          })
          .then(() => {
            console.log("##########");
            console.log(this.newComment.qno);
            this.getComments();
          });
      }
    },
    deleteComment(no) {
      var cno = no;
      console.log("***" + this.Qna.no);
      this.$store
        .dispatch(Constant.REMOVE_COMMENT, { cno: cno, qno: this.Qna.no })
        .then(() => {
          this.getComments();
        });
    }
  }
};
</script>

<style scoped></style>
